import React, { useState, useEffect, useCallback } from "react";
import { withRouter } from "react-router-dom";
import axios from "axios";
import { DataGrid, GridToolbar } from "@mui/x-data-grid";
import style from "./style.css";
import { Button } from "@mui/material";
import { Form, Container, Row, Col, Modal } from "react-bootstrap";
import Snackbar from "@mui/material/Snackbar";
import MuiAlert from "@mui/material/Alert";
import XLSX from "xlsx";
import Box from "@mui/material/Box";
import DialogContent from "@mui/material/DialogContent";
import CircularProgress from "@mui/material/CircularProgress";
import Dialog from "@mui/material/Dialog";

const Alert = React.forwardRef(function Alert(props, ref) {
  return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
});

function UpdateRateCard(props) {
  let [item, setItem] = useState([]);
  let [items, setItems] = useState([]);
  let [show, setShow] = useState(false);
  let [rateCardValidFrom, setRateCardValidFrom] = useState("");
  let [rateCardValidTo, setRateCardValidTo] = useState("");
  let [activeStatus, setActiveStatus] = useState(true);
  let [errors, setErrors] = useState({});
  let [open, setOpen] = useState(false);
  let [alart, setAlart] = useState("");
  let [severity, setSeverity] = useState("");
  let [openDialog, setOpenDialog] = useState(false);

  function modalClose() {
    setShow(false);
  }

  const findFormErrors = () => {
    const newErrors = {};
    // name errors
    // if (!companyName || companyName === '') newErrors.companyName = 'cannot be blank!'
    return newErrors;
  };

  function submitRateCard(e) {
    e.preventDefault();
    const newErrors = findFormErrors();
    // Conditional logic:
    // console.log(newErrors)
    if (Object.keys(newErrors).length > 0) {
      // We got errors!
      setErrors(newErrors);
    } else {
      setOpenDialog(true);
      axiosInstance
        .put(
          "projonline/updatRateCard/" + item._id,
          { rateCardValidFrom, rateCardValidTo, activeStatus },
          {
            headers: {
              token:
                "Bearer " +
                JSON.parse(localStorage.getItem("user")).accessToken,
            },
          }
        )
        .then((res) => {
          setSeverity(res.data.variant);
          setAlart("Rate Card Updated Successfully");
          setOpen(true);
          setRateCardValidFrom("");
          setRateCardValidTo("");
          setShow(false);
          getData();
          setOpenDialog(false);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }

  const toEdit = (data) => {
    setShow(true);
    setItem(data);
    setRateCardValidFrom(data.rateCardValidFrom.split("T")[0]);
    setRateCardValidTo(data.rateCardValidTo.split("T")[0]);
    setActiveStatus(data.activeStatus);

    // console.log(data)
  };

  const axiosInstance = axios.create({
    baseURL: process.env.REACT_APP_BACKEND_URL,
  });

  useEffect(() => {
    getData();
  }, []);

  const getData = (event, cellValues) => {
    setOpenDialog(true);
    axiosInstance
      .get("projonline/allRateCards", {
        headers: {
          token:
            "Bearer " + JSON.parse(localStorage.getItem("user")).accessToken,
        },
      })
      .then((res) => {
        setItems(res.data);
        setOpenDialog(false);
        // console.log(res.data)
        return;
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const downloadHandover = (data) => {
    //Create Downloadable excel file
    var workbook = XLSX.utils.book_new();

    //get selected projects
    var worksheetData = [];

    // console.log(data)
    data.rateCardItems.forEach((item) => {
      var row = [
        item.itemNo,
        item.itemDiscreption,
        item.erpCode,
        item.itemRate,
        item.itemUom,
      ];
      worksheetData.unshift(row);
    });

    var headers = [
      "itemNo",
      "itemDiscreption",
      "erpCode",
      "itemRate",
      "itemUom",
    ];
    worksheetData.unshift(headers);

    var ws = XLSX.utils.aoa_to_sheet(worksheetData);
    XLSX.utils.book_append_sheet(workbook, ws, "Rate Card Details");
    XLSX.writeFile(workbook, "Rate Card.xlsx");
  };

  const columns = [
    { field: "rateCardDiv", headerName: "Division", width: 250 },
    {
      field: "rateCardSubcon",
      headerName: "Subcontractor",
      valueGetter: (params) => params.row.rateCardSubcon.companyName,
      width: 200,
    },
    {
      field: "rateCardValidFrom",
      headerName: "Rate Card Valid From",
      valueGetter: (params) => params.row.rateCardValidFrom.split("T")[0],
      width: 250,
    },
    {
      field: "rateCardValidTo",
      headerName: "Rate Card Valid To",
      valueGetter: (params) => params.row.rateCardValidTo.split("T")[0],
      width: 250,
    },
    {
      field: "To Edit",
      width: 150,
      renderCell: (cellValues) => {
        return (
          <Button
            variant="contained"
            color="primary"
            onClick={(event) => toEdit(cellValues.row)}
          >
            To Edit
          </Button>
        );
      },
    },
    {
      field: "Download",
      width: 150,
      renderCell: (cellValues) => {
        return (
          <Button
            variant="contained"
            color="primary"
            onClick={(event) => downloadHandover(cellValues.row)}
          >
            Download
          </Button>
        );
      },
    },
  ];

  return (
    <div>
      {/* Content Wrapper. Contains page content */}
      <div className="content-wrapper">
        <div className="content-header">
          <section className="content-header">
            <div className="container-fluid">
              <div>
                <div>
                  <Container>
                    <Row>
                      <Col xs={2}>
                        <h1>All Rate Cards</h1>
                      </Col>

                      <Col xs={4}></Col>
                      <Col>
                        <div className="vr" />
                      </Col>
                      <Col>
                        <Button
                          variant="contained"
                          color="primary"
                          onClick={(e) =>
                            props.history.push({ pathname: "/addRateCard" })
                          }
                        >
                          Add a new Rate Card
                        </Button>
                      </Col>
                    </Row>
                  </Container>
                </div>
              </div>
            </div>
            {/* /.container-fluid */}
          </section>
        </div>

        <div style={{ height: 610, width: "100%" }}>
          <DataGrid
            getRowId={(item) => item._id}
            rows={items}
            columns={columns}
            pageSize={15}
            rowsPerPageOptions={[15]}
            rowHeight={30}
            headerHeight={40}
            components={{ Toolbar: GridToolbar }}
          />
        </div>
      </div>
      <Modal show={show} onHide={modalClose} animation={false}>
        <Modal.Header closeButton>
          <Modal.Title>Edit Rate Card</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form onSubmit={submitRateCard}>
            <Row className="mb-3">
              <Form.Group as={Col}>
                <Form.Label>Rate Card Valid From</Form.Label>
                <Form.Control
                  onChange={(e) => setRateCardValidFrom(e.target.value)}
                  value={rateCardValidFrom}
                  type="date"
                  placeholder="Enter Company Name"
                  isInvalid={!!errors.rateCardValidFrom}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.rateCardValidFrom}
                </Form.Control.Feedback>
              </Form.Group>
              <Form.Group as={Col}>
                <Form.Label>Rate Card Valid To</Form.Label>
                <Form.Control
                  onChange={(e) => setRateCardValidTo(e.target.value)}
                  value={rateCardValidTo}
                  type="date"
                  placeholder="Enter Company Name"
                  isInvalid={!!errors.rateCardValidTo}
                />
                <Form.Control.Feedback type="invalid">
                  {errors.rateCardValidTo}
                </Form.Control.Feedback>
              </Form.Group>
              <Form.Group as={Col}>
                <Form.Label>Active Status</Form.Label>
                <Form.Check
                  onChange={(e) => setActiveStatus(!activeStatus)}
                  value={activeStatus}
                  checked={activeStatus}
                  className="col-md-9"
                  id="custom-switch-2"
                  type="switch"
                />
              </Form.Group>
            </Row>

            <Modal.Footer>
              <Button variant="contained" color="primary" type="submit">
                Save
              </Button>
            </Modal.Footer>
          </Form>
        </Modal.Body>
      </Modal>
      <Snackbar
        open={open}
        autoHideDuration={6000}
        onClose={() => setOpen(false)}
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
      >
        <Alert
          onClose={() => setOpen(false)}
          severity={severity}
          sx={{ width: "100%" }}
        >
          {alart}
        </Alert>
      </Snackbar>

      <Dialog
        open={openDialog}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
      >
        <DialogContent>
          <Box sx={{ display: "flex" }}>
            <CircularProgress />
          </Box>
        </DialogContent>
      </Dialog>
    </div>
  );
}

export default withRouter(UpdateRateCard);
